'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Order, Payment, User } from '@/lib/types'
import { formatBDT } from '@/lib/mockData'

export default function EmployeeDashboard() {
  const [user, setUser] = useState<User | null>(null)
  const [orders, setOrders] = useState<Order[]>([])
  const [payments, setPayments] = useState<Payment[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState('')
  const [totalExpenses, setTotalExpenses] = useState(0)
  const [totalPaid, setTotalPaid] = useState(0)
  const [balance, setBalance] = useState(0)
  const router = useRouter()

  useEffect(() => {
    fetchUserData()
  }, [])

  const fetchUserData = async () => {
    try {
      setIsLoading(true)
      setError('')

      // Fetch authenticated user info
      const authResponse = await fetch('/api/auth')
      if (!authResponse.ok) {
        router.push('/login')
        return
      }
      const authData = await authResponse.json()
      setUser(authData.user)

      const employeeId = authData.user.employeeId || authData.user.id

      // Fetch orders for employee
      const ordersResponse = await fetch(`/api/order?employeeId=${employeeId}`)
      if (!ordersResponse.ok) {
        throw new Error('Failed to fetch orders')
      }
      const ordersData = await ordersResponse.json()
      setOrders(ordersData.orders || [])

      // Fetch payments for employee (simulate with mock data or API)
      // For demo, using mock payments from /api/payment or mockData
      const paymentsResponse = await fetch(`/api/payment?employeeId=${employeeId}`)
      if (paymentsResponse.ok) {
        const paymentsData = await paymentsResponse.json()
        setPayments(paymentsData.payments || [])
      } else {
        // If no payment API, simulate with empty array or mock data
        setPayments([])
      }

      // Calculate totals
      const expenses = (ordersData.orders || []).reduce((sum: number, order: Order) => sum + order.totalCost, 0)
      const paid = (paymentsResponse.ok ? (await paymentsResponse.json()).payments : []).reduce((sum: number, payment: Payment) => sum + payment.amount, 0)

      setTotalExpenses(expenses)
      setTotalPaid(paid)
      setBalance(paid - expenses)
    } catch (err) {
      console.error(err)
      setError('Failed to load employee data')
    } finally {
      setIsLoading(false)
    }
  }

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900 mx-auto mb-4"></div>
          <p>Loading your data...</p>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <h1 className="text-3xl font-bold mb-6">My Lunch Orders</h1>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
        <Card>
          <CardHeader>
            <CardTitle>Total Expenses</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-2xl font-bold text-red-600">{formatBDT(totalExpenses)}</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Total Paid</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-2xl font-bold text-green-600">{formatBDT(totalPaid)}</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Balance</CardTitle>
          </CardHeader>
          <CardContent>
            <p className={`text-2xl font-bold ${balance >= 0 ? 'text-green-600' : 'text-red-600'}`}>
              {formatBDT(balance)}
            </p>
          </CardContent>
        </Card>
      </div>

      <h2 className="text-2xl font-semibold mb-4">Order History</h2>
      {orders.length === 0 ? (
        <p>You have not placed any orders yet.</p>
      ) : (
        <div className="space-y-4">
          {orders.map((order) => (
            <Card key={order.id}>
              <CardHeader>
                <div className="flex justify-between items-center">
                  <p className="font-semibold">Order ID: {order.id}</p>
                  <p className="text-sm text-gray-600">{new Date(order.date).toLocaleDateString()}</p>
                </div>
              </CardHeader>
              <CardContent>
                <ul className="list-disc list-inside">
                  {order.menuItems.map((itemId, idx) => (
                    <li key={idx}>{itemId}</li> // Ideally, map itemId to item name
                  ))}
                </ul>
                <p className="mt-2 font-bold">Total Cost: {formatBDT(order.totalCost)}</p>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}
